import React, {useEffect, useState} from 'react';
import moment from 'moment';
import "./css/Clock.scss";

function Clock() {
  let timerId;
  const [time, setTime] = useState(GetState());
  useEffect(() => {
    timerId = setInterval(() => {
      CheckState();
    }, (1000));
    console.log("Started clock timer: " + timerId);
    return ()=>{
      if (timerId){
        console.log("Clearing clock timer: " + timerId);
        clearInterval(timerId);
      }
    }
  });


  function GetState() {
    var myMoment = moment();
    var result = {hour: myMoment.format("h"), minute: myMoment.format("mm"), meridian: myMoment.format("A")};
    return result;
  }

  function CheckState(){
    var newState = GetState();
    if (newState.minute != time.minute){
      setTime(newState)
    }
  }

  return(
    <div className="clock">
        <span>{time.hour}</span><span className="separator">:</span><span>{time.minute}&nbsp;</span><span>{time.meridian}</span>
    </div>
    );
}

export default (Clock)
