import React from 'react';
import Clock from './Clock';
import "./css/Banner.scss";

function Banner(props){
    const myStyle = {height: props.height};
    
    return(
<div className="banner banner-landscape-allow" style={myStyle}>
    <div className="banner-overlay" style={{backgroundColor: props.bgColor}}>
        <div className="heading">
            <div className="heading-message" style={myStyle}>
                <span className="title text-uppercase">{props.title}</span>
                <span className="subtitle text-ellipsis">
                    {props.subtitle}
                </span>
            </div>
            <div className="logo-container">
                <div>
                    <div className="clock-underline"></div>
                    <Clock />
                </div>
            </div>
        </div>
    </div>
</div>
    );
}


export default (Banner)
