let hubUrl = "https://ddcgenlite2-cqenbrbmcacahrfr.centralus-01.azurewebsites.net/deviceHub?config=";
let self;


const GetUncompressed = function(base64Data){
    const strData = atob(base64Data);
    const charData = strData.split('').map(function (x) { return x.charCodeAt(0); });
    const binData = new Uint8Array(charData);
    const data = pako.inflateRaw(binData);
    var data2 = new TextDecoder("utf-8").decode(data);
    return data2;
}

function HubListener() {
    console.log("Creating new HubListener");
    const queryParameters = new URLSearchParams(window.location.search);
    var configName = queryParameters.get("config");
    if (!configName) configName = "ScheduleSort";
    console.log("Config name: " + configName);
    hubUrl += configName;
    let connection = null;
    self = this;
    //parent = props.parent;
    let flightDataset = [];
    let fields = [];
    const flightEventName = "flightsChanged";

    const FieldSubject = new Subject();
    const FieldObservable = FieldSubject.asObservable();
    const FlightSubject = new Subject();
    const FlightObservable = FlightSubject.asObservable();

    

    function init() {  
        connection = new signalR.HubConnectionBuilder()
        .withUrl(hubUrl)
        //.withAutomaticReconnect({nextRetryDelayInMilliseconds: ctx => 15000})
        .build();
    
        console.log("Listener mount");
        start();
    }



    function TriggerFlightUpdate(newFlights){
        flightDataset = newFlights;
        // if (setFlights)
        //     setFlights(newFlights.data);

        FlightSubject.next(newFlights.data);
    }

    function TriggerFieldUpdate(newFields) {
        var length = Object.keys(newFields).length;
        console.log("Updating fields with " + length + " records");
        fields = newFields;
        // if (setFields)
        //     setFields(newFields);

        FieldSubject.next(newFields);
    }


    function stop() {
        if (connection){
            console.log("Stopping hub connection");
            connection.stop();
        }
    }

    function start(){
        connection.on("flights", (data) =>{
            //var flights = data.data["0"];
            var flights = data;
            console.log("flights");
            TriggerFlightUpdate(flights);
        });

        connection.on("fields", (fields) => {
        
            console.log("fields");
            TriggerFieldUpdate(fields);
        });

        connection.on("zip", (type, base64data) =>{
            
            const json = GetUncompressed(base64data);
            console.log("Received zip - Compressed: "  + base64data.length + " bytes. Uncompressed: " + json.length + " bytes");
            const data = JSON.parse(json);
            switch(type){
                case "fields":
                    console.log("processing fields");
                    TriggerFieldUpdate(data);
                    break;
                case "flights":
                    console.log("processing DataSet");
                    TriggerFlightUpdate(data);
                    break;
                default:
                    console.log("Unrecognized zip type: " + type);
            }
        });
        
        connection.on("flightUpdates", (dto) => {
            console.log("flightUpdates");
            const localFlights = flightDataset.data;
            const data = dto.data;
            
            if (data && data.length > 0) {
                var newFlightHash = {};
                const newFlights = [];
                for (let i = 0; i < data.length; i++){
                    var newFlight = data[i];
                    newFlightHash[newFlight.id] = newFlight;
                    console.log("Updating flight " + newFlight.id);
                }

                for (let j = 0; j < localFlights.length; j++){
                    var refFlight = localFlights[j];
                    var updatedFlight = newFlightHash[refFlight.id];
                    if (updatedFlight)
                        newFlights.push(updatedFlight);
                    else
                        newFlights.push(refFlight);
                }
            
                var newDto = {name: dto.name, data: newFlights};
                console.log("newFlights length: " + newFlights.length);
                TriggerFlightUpdate(newDto);
            }
        });
        
        connection.onclose((e) => {
            console.log("Hub disconnected.");
            //setTimeout(startConnection, 15000); 
        });

        connection.onreconnecting((error) => {
            console.log("Reconnecting");
        });

        connection.onreconnected((connectionId) => {
            console.log("Hub reconnected - Subscribing for template data.");
            //subscribeForData();
        });

        startConnection();
        setInterval(() => {
            checkConnection();
        }, (15000));
    }

    async function checkConnection(){
        if (connection.state !== signalR.HubConnectionState.Connected){
            console.log("Signalr state: " + connection.state + " - will attempt to reconnect");
            await startConnection();
        }
    }

    async function startConnection(){
        try{
            await connection.start();
            console.log("Hub connected");
            //self.subscribeForData();
        }
        catch (ex){
            console.error(ex);
            console.log("Hub connection failure.");
            //setTimeout(startConnection, 15000);    
        }
    }

    return {init, stop, FieldObservable, FlightObservable};
}

const hubInstance = new HubListener();
export { hubInstance };