import React from 'react';
import FlightDetail from './FlightDetail';
import Banner from './Banner';
require("./css/ArrivalDeparture.min.css");


function ArrivalDeparture(props) {

      const flights = props.flights;
      const fields = props.fields;
    return(
<div>
    <div className="content">
        <div className="container">
            <div className="columns col-gapless">
                <div className="column col-4 col-xl-12">
                    <Banner title="DEPARTURES" subtitle="Where am I going?" clockFormat="h:mm tt" />
                </div>
                <div className="flight-container column col-8 col-xl-12">
                    <div className="column-headings col-12">
                        <div className="columns">
                            <div className="column col-3 text-left">CITY</div>
                            <div className="column col-3 text-center">AIRLINE</div>
                            <div className="column col-3">
                                <div className="columns">
                                    <div className="column col-6 text-left">FLIGHT</div>
                                    <div className="column col-6 text-center">GATE</div>
                                </div>
                            </div>
                            <div className="column col-3 text-center">STATUS</div>
                        </div>
                    </div>
                    <div className="pages-container">
                        <div className="page">
                            <div>
                                        {flights.map((flight) =>
                                            <FlightDetail key={flight.id} flight={flight} fields={fields} />
                                )}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
    );
}

export default (ArrivalDeparture)
