import moment from 'moment';
import React from 'react';
require("./css/FlightDetail.min.css");

const timeFormat = "h:mm A";

function FlightDetail(props) {

  function formatTime(value){
    var result =moment(value).format(timeFormat);
    return result;
  }

  function getLookup(field, valueType, language){
        if (!valueType) valueType = "default";
        if (!language) language = "en";
        var dailyValue = getDailyValue(field);
        var key = `${field}/${dailyValue}/${language}/${valueType}`.toLowerCase();
        var fields = props.fields;
        var result = fields[key]?.value ?? dailyValue;
        return result;

  }

  function getDailyValue(field) {
    var flight = props.flight;
    var result = flight?.fields?.[field]?.[0]?.value;
    return result;
  }

  function getCodeshareField(field) {
    var flight = props.flight;
    var result = flight?.fieldGroups?.codeshares?.[0]?.[field]?.value;
    return result;
  }

    const flightRecord = props.flight;
    if (flightRecord.longText?.length > 0){
        console.log("Flight " + flightRecord.id + " has long text: " + flightRecord.longText);
        return <div style={{textAlign: 'center', lineHeight: '60px'}}>{flightRecord.longText}</div>
    }
    const fields = props.fields;
    const theme = "light";
    const imageBaseUrl = "https://abc.dev.cloudids.aero/images/";
    var flight = {
        id: flightRecord.id, 
        schedule: formatTime(flightRecord?.schedule),
        city: getLookup("city"),
        logo: imageBaseUrl + getCodeshareField("logo"),
        number: getCodeshareField("number"),
        gate: getDailyValue("gate"),
        status: getLookup("status"),
        statusClass: getLookup("status", "templatestatusclass")
    };

return(
<div data-id={flight.id} className="flightdetail">
    <div>
        <div className={`flight-card-${theme}`}>
            <div className="columns">
                <div className="column col-3 text-left ">
                    <div className="time">{flight.schedule}</div>
                    <div className="text-bold text-uppercase city">{flight.city}</div>
                </div>
                <div className="column col-6">
                    <div className="columns">
                        <div className="column col-6 airline-icon text-center">
                            <div className="airline-icon logo-container">
                                <img src={flight.logo} />
                            </div>
                        </div>
                        <div className="column col-6  text-left center">
                            <div className="columns">
                                <div className={`column col-6 text-left flight flight-${theme} center flight-row-height`}>
                                    {flight.number}
                                </div>
                                <div className="column col-6 text-center flight-row-height">
                                    <span className=" text-bold gate center">
                                        {flight.gate}
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className={`column col-3 text-center text-uppercase center status flight-row-height flight-status-${theme}-${flight.statusClass} flight-status-${theme}-default`}>
                    <div>{flight.status}</div>
                </div>
            </div>
        </div>
    </div>
</div>
    );
}


export default (FlightDetail)