import ArrivalDeparture from './ArrivalDeparture'
import { hubInstance as hubListener} from './HubListener'; 
import React, {useState, useEffect} from 'react';
import { Observable } from 'rxjs';


function MainContainer(){
    const [flights, setFlights] = useState([]);
    const [fields, setFields] = useState([]);

    useEffect(() => {
        //hubListener = new HubListener(setFlights, setFields);
        //hubListener = new HubListener();
        hubListener.init(); 

        hubListener.FlightObservable.subscribe({
            next(flights) {
                console.log("flight observable updated");
                setFlights(flights);
            }
        });

        hubListener.FieldObservable.subscribe({
            next(fields) {
                console.log("Field observable updated");
                setFields(fields);
            }
        });
      }, []
    );


    return (
        <>
            <ArrivalDeparture flights={flights} fields={fields} />
        </>
        
    );
}


export default MainContainer;
